<template>
  <model-select :options="searchList" :is-disabled="disabled" v-model="item">
  </model-select>
</template>

<script>
import { ModelSelect } from "vue-search-select";
import "vue-search-select/dist/VueSearchSelect.css";
export default {
  name: "DropDownSearch",
  components: {
    ModelSelect,
  },
  props: {
    searchList: {
      type: [Array],
      default: () => [],
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    defaultItem: {
      type: [Object],
      default: () => {},
    },
  },

  data() {
    return {
      item: {
        value: "",
        text: "",
      },
    };
  },
  watch: {
    item: function () {
      this.$emit("selected_person_data", this.item);
    },
  },
  mounted() {
  },
};
</script>
<style lang="scss" scoped>
.ui.disabled.dropdown,
.ui.dropdown .menu > .disabled.item {
  border: 1px solid #6b6267 !important;
}
</style>